          align: 'center'
        },
        {
          title: '(hickwall)',
          slot: 'panel',
          align: 'center'
        }

      ],
      // page
      total: 0,
      current: 1,
      size: 10,
      // query param
      srcTbl: this.$route.query.srcTbl,
      dstTopic: this.$route.query.dstTopic,
      dbNames: this.$route.query.dbNames,
      otterSearchMode: this.$route.query.otterSearchMode === true || this.$route.query.otterSearchMode === 'true',
      // get from backend
      totalCount: [{
        otterDbCount: null,
        otterTopicCount: null,
        messengerDbCount: null,
        messengerTopicCount: null
      }],
      records: [],

      dataLoading: false,
      totalCountLoading: false
    }
  },
  computed: {
    dataWithPage () {
      const data = this.records
      const start = this.current * this.size - this.size
      const end = start + this.size
      return [...data].slice(start, end)
    }
  },
  methods: {
    async getReplications () {
      const that = this
      if (that.dataLoading) return false
      that.dataLoading = true
      const params = {
        dbNames: this.dbNames,
        topic: this.dstTopic,
        srcTblName: this.srcTbl
      }
      if (this.otterSearchMode) {
        params.queryOtter = true
      } else {
        params.queryOtter = false
      }
      const reqParam = this.flattenObj(params)
      console.log(reqParam)
      await that.axios.get('/api/drc/v2/messenger/ReplicationSyncStatus', { params: reqParam })
        .then(res => {
          const pageResult = res.data.data
          if (!pageResult || pageResult.totalCount === 0) {
            that.total = 0
            that.current = 1
            that.records = []
            that.$Message.warning('')
          } else {
            that.records = pageResult
            that.total = pageResult.length
            that.current = 1
            that.$Message.success('')
          }
        })
        .catch(message => {
          that.$Message.error(': ' + message)
        })
        .finally(() => {
          that.dataLoading = false
        })
    },
    flattenObj (ob) {
      const result = {}
      for (const i in ob) {
        if ((typeof ob[i]) === 'object' && !Array.isArray(ob[i])) {
          const temp = this.flattenObj(ob[i])
          for (const j in temp) {
            result[i + '.' + j] = temp[j]
          }
        } else {
          result[i] = ob[i]
        }
      }
      return result
    },
    handleChangeSize (val) {
      this.size = val

      this.$nextTick(() => {
        this.getReplications()
      })
    },
    // async getTotalCount () {
    //   console.log('begin getTotalCount')
    //   const params = {
    //     dbNames: this.dbNames,
    //     topic: this.dstTopic,
    //     srcTblName: this.srcTbl,
    //     queryOtter: false
    //   }
    //   const that = this
    //   that.totalCountLoading = true
    //   that.dataLoading = true
    //   await that.axios.get('/api/drc/v2/messenger/ReplicationSyncStatus', { params: params }).then(res => {
    //     const pageResult = res.data.data
    //     const uniqueMessengerDb = new Set()
    //     const uniqueMessengerTopic = new Set()
    //     const uniqueOtterDb = new Set()
    //     const uniqueOtterTopic = new Set()
    //     if (!pageResult || pageResult.totalCount === 0) {
    //       that.total = 0
    //       that.current = 1
    //       that.records = []
    //       that.totalCount.messengerDbCount = 0
    //       that.totalCount.messengerTopicCount = 0
    //       that.totalCount.otterDbCount = 0
    //       that.totalCount.otterTopicCount = 0
    //     } else {
    //       pageResult.forEach(obj => {
    //         uniqueMessengerDb.add(obj.dbName)
    //         uniqueMessengerTopic.add(obj.dstLogicTableName)
    //         if (obj.dstLogicTableName.includes('otter') || !obj.dstLogicTableName.endsWith('.binlog')) {
    //           uniqueOtterDb.add(obj.dbName)
    //           uniqueOtterTopic.add(obj.dstLogicTableName)
    //         }
    //       })
    //       that.totalCount = [{
    //         otterDbCount: uniqueOtterDb.size,
    //         otterTopicCount: uniqueOtterTopic.size,
    //         messengerDbCount: uniqueMessengerDb.size,
    //         messengerTopicCount: uniqueMessengerTopic.size
    //       }]
    //       that.records = pageResult
    //       that.total = pageResult.length
    //     }
    //   })
    //     .catch(message => {
    //       that.$Message.error(': ' + message)
    //     })
    //     .finally(() => {
    //       that.totalCountLoading = false
    //       that.dataLoading = false
    //     })
    // },
    resetParam () {
      this.srcTbl = null
      this.dstTopic = null
      this.dbNames = null
    },
    goToHickwallMonitorPageDelay (row, index) {
      console.log(row)
      this.$Message.info('...')
      this.$Modal.remove()
      // console.log(srcName, dstName)
      this.$forceUpdate()
      const url = 'https://bat.fws.qa.nt.ctripcorp.com/d/qFmdZpCSz/drc-mha-qmq?orgId=0&var-mha=' + row.mhaName + '&var-auto_gen_other_mha=All'
      // const url = 'https://bat.fx.ctripcorp.com/d/vWeGmjFVk/drc-mha?from=now-24h&to=now&var-auto_gen_other_mha=All&var-mha=sgpfltpub05&orgId=0&refresh=30s&viewPanel=20'
      window.open(url, '_blank')
      // console.log('go to change config for ' + row.srcMha.name + ' and ' + row.destMha)
