      //     dstMhaName: row.dstMha.name
      //   }
      // })
    }
  },
  created () {
    console.log('created()')
    this.axios.get('/api/drc/v2/permission/meta/dbReplication/query').then((response) => {
      if (response.data.status === 403) {
        this.$router.push('/nopermission')
        return
      }
      this.getReplications()
    })
  }
